import Vue from 'vue'
import Vuex from 'vuex'
import router from '@/router'
import clone from '@/lib/clone'
import createId from '@/lib/createId';

Vue.use(Vuex) //

const store = new Vuex.Store({
  state: {
    recordList: [],
    tagList: [],
    currentTag: undefined,
    create: false,
    createRecordError: new Error()
  } as RootState,
  mutations: {
    // recordList方法
    fetchRecords(state) {
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[];
    },
    createRecord(state, record) {
      const record2: RecordItem = clone(record);
      record2.createdAt = new Date().toISOString();
      state.recordList.push(record2);
      store.commit('saveRecords')
      window.alert('已保存')
    },
    saveRecords(state) {
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList));
    },
    // tagList方法
    fetchTags(state) {
      if (!window.localStorage.getItem('create')) {
        state.create = true
      } // 防止全部删除后四个标签又重复出现的bug
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
      if ((!state.tagList || state.tagList.length === 0) && state.create === true) {
        state.create = false
        window.localStorage.setItem('create', 'initComplete')// 防止全部删除后四个标签又重复出现的bug
        store.commit('createTag', '衣');
        store.commit('createTag', '食');
        store.commit('createTag', '住');
        store.commit('createTag', '行');
        state.createRecordError = null
      }
    },
    setCurrentTag(state, id) {
      state.currentTag = state.tagList.filter(t => t.id === id)[0];
    },
    createTag(state, name: string) {
      const names = state.tagList.map(item => item.name);
      if (names.indexOf(name) >= 0) {
        window.alert('标签名重复了');
      } else {
        const id = createId().toString();
        state.tagList.push({ id, name: name });
        store.commit('saveTags');
        if (state.createRecordError === null) {
          window.alert('已创建标签')
        }
      }
    },
    removeTag(state, id: string) {
      let index = -1;
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i;
          break;
        }
      }
      if (index >= 0) {
        state.tagList.splice(index, 1);
        store.commit('saveTags');
        router.push('/labels')
      } else {
        window.alert('删除失败')
      }
    },
    updateTag(state, object: { id: string; name: string }) {
      const { id, name } = object
      const idList = state.tagList.map(item => item.id);
      if (idList.indexOf(id) >= 0) {
        const names = state.tagList.map(item => item.name);
        if (names.indexOf(name) >= 0) {
          window.alert('标签名重复')
        } else {
          const tag = state.tagList.filter(item => item.id === id)[0];
          tag.name = name;
          store.commit('saveTags');
        }
      }
    },
    saveTags(state) {
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store